import { html, LitElement } from "lit";
import { customElement, property } from "lit/decorators";
import { ArrayConverter } from "../_Converters/ArrayConverter";

declare var juel: any;

@customElement("juel-icons")
export class JuelIcons extends LitElement {

    @property({ converter: ArrayConverter() }) icons: string[];

    createRenderRoot() {
        return this;
    }

    protected updated(): void {
        if (this.icons && this.icons) {
        let el = $(this.querySelector("#juel-icons"));
        el.empty();
        for (let name of this.icons) {
            let svg = juel.icon.get(name);
            if (svg) {
                el.append(svg);
            }
        }
    }
    }

    render() {
        return html`<div id="juel-icons" class="hidden"></div>`
    }
}